/*
  				
  title	MessageBox 	string		
  message	MessageBox 	string / VNode		
  dangerouslyUseHTMLString	 message  HTML 	boolean		false
  type		string	success / info / warning / error	
  iconClass	 type	string		
  customClass	MessageBox 	string		
  callback	 Promise MessageBox 	function(action, instance)action 'confirm', 'cancel''close', instance  MessageBox 		
  showClose	MessageBox 	boolean		true
  beforeClose	MessageBox 	function(action, instance, done)action 'confirm', 'cancel''close'instance  MessageBox done  MessageBox 		
  distinguishCancelAndClose	 ESC 	boolean		false
  lockScroll	 MessageBox  body 	boolean		true
  showCancelButton		boolean		false confirm  prompt  true
  showConfirmButton		boolean		true
  cancelButtonText		string		
  confirmButtonText		string		
  cancelButtonClass		string		
  confirmButtonClass		string		
  closeOnClickModal	 MessageBox	boolean		true alert  false
  closeOnPressEscape	 ESC  MessageBox	boolean		true alert  false
  closeOnHashChange	 hashchange  MessageBox	boolean		true
  showInput		boolean		false prompt  true
  inputPlaceholder		string		
  inputType		string		text
  inputValue		string		
  inputPattern		regexp		
  inputValidator	,  inputErrorMessage	function		
  inputErrorMessage		string		!
  center		boolean		false
  roundButton		boolean		false
 */

export default {
  
  confirmBox({
    component = null,
    componentName = '',
    componentProps = {},
    confirmValidate = () => {},
    ...rest
  }) {
    const h = this.$createElement
    return new Promise((resolve, reject) => {
      this.$msgbox({
        title: '',
        message: h(component, { props: { componentProps }}),
        showCancelButton: true,
        beforeClose: (action, instance, done) => {
          const cpt = instance.$children.find(child => {
            return child.$options.name === component.name
          })
          confirmValidate(action, cpt, done)
        },
        ...rest
      })
        .then(resolve)
        .catch(reject)
    })
  }
}
